// src/pages/api/orders/index.js
import mongoose from "mongoose";
import dotenv from "dotenv";
import Product from "../../../models/productModel.js";
dotenv.config();

/**
 * Orders API:
 * - tries to use transactions (if supported)
 * - falls back to conditional updates + rollback if transactions unavailable
 */

async function createOrderDoc(data, session = null) {
  if (!mongoose.models.Order) {
    const OrderSchema = new mongoose.Schema({
      customer: { name: String, phone: String, address: String },
      items: [{ productId: mongoose.Schema.Types.ObjectId, name: String, qty: Number, price: Number, size: String }],
      total: Number,
      status: { type: String, default: "Pending" }
    }, { timestamps: true });
    mongoose.model("Order", OrderSchema);
  }
  const Order = mongoose.model("Order");
  if (session) {
    const docs = await Order.create([data], { session });
    return docs[0];
  } else {
    return await Order.create(data);
  }
}

export default async function handler(req, res) {
  if (!mongoose.connection.readyState) await mongoose.connect(process.env.MONGO_URI);

  if (req.method !== "POST") return res.status(405).json({ message: "Method not allowed" });

  const payload = req.body;
  if (!payload || !payload.customer || !Array.isArray(payload.items) || payload.items.length === 0) {
    return res.status(400).json({ message: "Invalid order payload" });
  }

  // Try transaction first
  let session = null;
  try {
    session = await mongoose.startSession();
    session.startTransaction();

    const insufficient = [];

    for (const item of payload.items) {
      const productId = item.productId;
      const qty = Number(item.qty) || 0;
      const sizeLabel = item.size || null;

      if (!productId || qty <= 0) {
        insufficient.push({ productId, reason: "invalid-qty" });
        break;
      }

      if (sizeLabel) {
        const result = await Product.updateOne(
          { _id: productId, sizes: { $elemMatch: { label: sizeLabel, stock: { $gte: qty } } } },
          { $inc: { "sizes.$.stock": -qty } },
          { session }
        );
        if (!result.modifiedCount) {
          insufficient.push({ productId, size: sizeLabel, requested: qty });
          break;
        }
      } else if (typeof item._usesTopLevelStock !== "undefined" && item._usesTopLevelStock) {
        const result = await Product.updateOne(
          { _id: productId, stock: { $gte: qty } },
          { $inc: { stock: -qty } },
          { session }
        );
        if (!result.modifiedCount) {
          insufficient.push({ productId, requested: qty });
          break;
        }
      } else {
        // if no stock tracking for this product, skip decrement
      }
    }

    if (insufficient.length) {
      await session.abortTransaction();
      session.endSession();
      return res.status(400).json({ message: "Insufficient stock", details: insufficient });
    }

    // create order doc
    const order = await createOrderDoc({
      customer: payload.customer,
      items: payload.items.map(i => ({ productId: i.productId, name: i.name || "", qty: Number(i.qty) || 1, price: Number(i.price) || 0, size: i.size || null })),
      total: Number(payload.total) || 0,
      status: "Pending"
    }, session);

    await session.commitTransaction();
    session.endSession();
    return res.status(201).json({ message: "Order placed", orderId: order._id });
  } catch (errTransaction) {
    console.warn("Transaction failed or not supported, attempting fallback. Error:", errTransaction && errTransaction.message);

    // Attempt fallback: conditional updates without session, with simple rollback if necessary
    try {
      // we'll record successful decrements to rollback if needed
      const decremented = []; // { productId, size, qty }
      const insufficient = [];

      for (const item of payload.items) {
        const productId = item.productId;
        const qty = Number(item.qty) || 0;
        const sizeLabel = item.size || null;

        if (sizeLabel) {
          const result = await Product.updateOne(
            { _id: productId, sizes: { $elemMatch: { label: sizeLabel, stock: { $gte: qty } } } },
            { $inc: { "sizes.$.stock": -qty } }
          );
          if (!result.modifiedCount) {
            insufficient.push({ productId, size: sizeLabel, requested: qty });
            break;
          } else {
            decremented.push({ productId, size: sizeLabel, qty });
          }
        } else if (typeof item._usesTopLevelStock !== "undefined" && item._usesTopLevelStock) {
          const result = await Product.updateOne({ _id: productId, stock: { $gte: qty } }, { $inc: { stock: -qty } });
          if (!result.modifiedCount) {
            insufficient.push({ productId, requested: qty });
            break;
          } else {
            decremented.push({ productId, qty });
          }
        } else {
          // skip
        }
      }

      if (insufficient.length) {
        // rollback decrements
        for (const d of decremented) {
          if (d.size) {
            await Product.updateOne({ _id: d.productId, sizes: { $elemMatch: { label: d.size } } }, { $inc: { "sizes.$.stock": d.qty } });
          } else {
            await Product.updateOne({ _id: d.productId }, { $inc: { stock: d.qty } });
          }
        }
        return res.status(400).json({ message: "Insufficient stock", details: insufficient });
      }

      // all good â€” create order doc (no session)
      const order = await createOrderDoc({
        customer: payload.customer,
        items: payload.items.map(i => ({ productId: i.productId, name: i.name || "", qty: Number(i.qty) || 1, price: Number(i.price) || 0, size: i.size || null })),
        total: Number(payload.total) || 0,
        status: "Pending"
      }, null);

      return res.status(201).json({ message: "Order placed", orderId: order._id });
    } catch (errFallback) {
      console.error("Fallback order creation failed:", errFallback);
      return res.status(500).json({ message: "Failed to place order" });
    }
  }
}
