// models/productModel.js
import mongoose from "mongoose";

/**
 * Product model
 * - sizes: array with {label, stock}
 * - categories: ObjectId refs to Category
 */

const SizeSchema = new mongoose.Schema({
  label: { type: String, required: true },
  stock: { type: Number, default: 0, min: 0 }
}, { _id: false });

const ProductSchema = new mongoose.Schema({
  name: { type: String, required: true, trim: true, text: true },
  description: { type: String, trim: true, default: "" , text: true},
  sku: { type: String, trim: true, index: true, sparse: true },
  price: { type: Number, required: true },
  originalPrice: { type: Number },
  currency: { type: String, default: "INR" },
  images: { type: [String], default: [] },
  sizes: { type: [SizeSchema], default: [] }, // inventory per size
  // normalized categories
  categories: [{ type: mongoose.Schema.Types.ObjectId, ref: "Category" }],
  primaryCategory: { type: mongoose.Schema.Types.ObjectId, ref: "Category", default: null },
  storefronts: { type: [String], default: ["livemart"] },
  productType: { type: String, enum: ["clothing", "tech", "grocery", "other"], default: "other" },
  // optional top-level stock (if you want)
  stock: { type: Number, default: undefined },
  isActive: { type: Boolean, default: true },
  createdBy: { type: mongoose.Schema.Types.ObjectId, ref: "User", required: false }
}, { timestamps: true });

ProductSchema.index({ name: "text", description: "text" });
ProductSchema.index({ categories: 1 });
ProductSchema.index({ primaryCategory: 1 });
ProductSchema.index({ storefronts: 1 });
ProductSchema.index({ productType: 1 });

ProductSchema.pre("save", function (next) {
  if (!this.storefronts || !Array.isArray(this.storefronts)) this.storefronts = ["livemart"];
  this.storefronts = this.storefronts.map(s => String(s).trim().toLowerCase()).filter(Boolean);
  if (!this.categories) this.categories = [];
  this.categories = this.categories.filter(c => !(typeof c === "string" && c.trim() === ""));
  if (this.name) this.name = String(this.name).trim();
  if (this.description) this.description = String(this.description).trim();
  next();
});

ProductSchema.methods.totalStock = function () {
  if (Array.isArray(this.sizes) && this.sizes.length) return this.sizes.reduce((s, x) => s + (Number(x.stock) || 0), 0);
  if (typeof this.stock === "number") return this.stock;
  return 0;
};

export default mongoose.models.Product || mongoose.model("Product", ProductSchema);
